<script>
import global from "../global";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"
import Logo from "../../components/logo/Logo";
import anime from "../../../modules/anime";

let idea_anime_end = false;

//export default
export default {
  name: "Contact",
  data() {
    return {
      global: global,
      idea: [],
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer,
    Logo
  },
  beforeCreate() {
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    this.start_idea_anime()
  },
  destroyed() {
    idea_anime_end = true;
  },
  watch: {
  },
  methods: {
    start_idea_anime() {
      let that = this;
      let idx = 0;
      let idea_list = this.global.lang_pack.contact_idea_list;
      idea_anime_end = false;
      let change_fun = function () {
        let idea = idea_list[idx];
        idx++;
        idx %= idea_list.length;
        that.idea = [];
        if (document.getElementById("contact_idea_bg") === null)
          return
        document.getElementById("contact_idea_bg").style.width = "0px";
        for (let i = 0; i < idea.length; i++) {
          that.idea.push({
            idx: i,
            letter: idea[i],
            visibility: "hidden",
          });
        }

        for (let i = 0; i < idea.length; i++) {
          setTimeout(() => {
            that.idea[i].visibility = "visible";
            if (i === idea.length - 1) {
              if (document.getElementById("contact_idea_box") === null)
                return
              let bg_width = document.getElementById("contact_idea_box").clientWidth;
              document.getElementById("contact_idea_bg").style.right = "calc(100% - " + (bg_width + 5)+ "px)"
              new anime({
                targets: "div.contact_idea_bg",
                width: (bg_width + 10) + "px",
                delay: 100,
                duration: idea.length * 100,
                easing: "linear",
              });
            }
          }, i * 150);
        }

        if (idea_anime_end === false) {
          let t = {t}
          new anime({
            targets: t,
            t: [0,0],
            duration: idea.length * 250 + 200,
            easing: "linear",
            complete: function (){
              change_fun()
            }
          });
        }
      };
      change_fun();
    },
  },
};
</script>



